<template>
  <div id="app">
    : <input
      v-model="message"
      type="text"
      @keyup="sendMessage"
    >
    <div
      v-for="(item, idx) in recvList"
      :key="idx"
    >
      <div class="message">
        <h3>: {{ item.senderId }}</h3>
        <h3>: {{ item.message }}</h3>
      </div>
    </div>
  </div>
</template>

<script>
import Stomp from 'webstomp-client'
import SockJS from 'sockjs-client'
import * as chatApi from '@/api/chat';
import { mapState } from 'vuex';
import {User_getStudentId, User_showNickname} from '@/store/store'

export default {
  name: 'App',
  props:{
    roomId: String,
  },
  computed: {
    ...mapState({
      studentNumber: User_getStudentId,
    }),
  },
  data() {
    return {
      message: "",
      recvList: []
    }
  },
  created() {
    //    .
    this.loadPrevMessage();

    // App.vue    .
    this.connect()
  },
  methods: {
    async loadPrevMessage() {
      const res = await chatApi.loadPrevMessage(this.roomId);
      //  
      const objs = res.data;
      console.log(objs);
      for (let i = 0; i < objs.length; i++) {
        this.recvList.push(objs[i]);
      }
    },
    sendMessage (e) {
      if(e.keyCode === 13 && this.message !== ''){
        this.send()
        this.message = ''
      }
    },    
    send() {
      console.log("Send message:" + this.message);
      if (this.stompClient && this.stompClient.connected) {
        const msg = { 
          subjectId: this.roomId,
          nickname: this.$store.state.user.nickname,
          senderId: this.$store.state.user.studentId,
          message: this.message,
          createdTime: null
        };
        this.stompClient.send("/receive", JSON.stringify(msg), {});
      }
    },    
    connect() {
      //   endpoint /ws/chat
      const serverURL = "http://localhost:8080/ws/chat"
      let socket = new SockJS(serverURL);
      this.stompClient = Stomp.over(socket);
      this.stompClient.connect(
        {},
        frame => {
          //   
          this.connected = true;
          console.log('  ', frame);
          //    endpoint .
          // /topic/chat  /app/topic/chat
          this.stompClient.subscribe("/topic/chat/room/" + this.roomId, res => {
            console.log('   .', res.body);

            //   json   .
            this.recvList.push(JSON.parse(res.body))
          });
        },
        error => {
          //   
          console.log('  ', error);
          this.connected = false;
        }
      );        
    }
  }
}
</script>

<style scoped>
#app {
  padding: 15px;
  margin-top: 15px;
  margin-bottom: 0;
}
.message{
  margin-top: 7px;
  margin-bottom: 7px;
}
</style>



